:root {
  --font-main: "Kumbh Sans", sans-serif;
}

:root {
  --orange: hsl(26, 100%, 55%);
  --pale-orange: hsl(25, 100%, 94%);
  --very-dark-blue: hsl(220, 13%, 13%);
  --dark-grayish-blue: hsl(219, 9%, 45%);
  --grayish-blue: hsl(220, 14%, 75%);
  --light-grayish-blue: hsl(223, 64%, 98%);
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}

body {
  font-family: var(--font-main);
  font-size: 16px;
}

h3 {
  margin: 0;
  font-size: 1rem;
}

a {
  text-decoration: none;
  color: inherit;
}

.cart h3 {
  padding: 1.5rem;
  border-bottom: 1px solid var(--grayish-blue);
}

.cart-content .cart-total {
  font-weight: 700;
  color: var(--black);
}

.cart-content p {
  margin: 3rem auto;
  color: var(--dark-grayish-blue);
}

.info {
  max-width: 40em;
  margin: auto;
}
.info p {
  line-height: 1.5;
  color: var(--dark-grayish-blue);
}

.title {
  font-size: 0.875rem;
  letter-spacing: 2px;
  color: var(--orange);
  text-transform: uppercase;
}

ul {
  list-style: none;
  display: flex;
}

img {
  display: block;
  max-width: 100%;
}

.container {
  padding: 1.5rem;
  margin: auto;
}

.info {
  max-width: 40em;
  margin: auto;
}

@media all and (min-width: 960px) {
  header {
    padding: 0 1rem;
  }

  h1 {
    font-size: 2.5rem;
  }

  .nav-open-btn,
.nav-close-btn {
    display: none;
  }

  .desktop-nav ul {
    display: flex;
  }

  .cart {
    top: 58px;
  }

  .profile-img {
    height: 3.125rem;
    width: 3.125rem;
  }

  .container {
    padding: 0;
  }

  .container-main {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 7rem;
    padding: 3rem;
    max-width: 1110px;
    margin: auto;
  }

  .lightbox {
    max-width: 28rem;
  }

  .lightbox-main {
    max-height: 28rem;
  }

  .product-image-main {
    max-height: 28rem;
    border-radius: 16px;
  }

  .arrow-left,
.arrow-right {
    display: none;
  }

  .lightbox-thumbnails {
    display: flex;
  }

  .price-wrapper {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }

  .add-to-cart-wrapper {
    grid-template-columns: minmax(160px, 1fr) 2fr;
    margin-bottom: 0;
  }

  .overlay {
    display: flex;
  }
}
button {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 1.25rem;
  width: 100%;
  color: var(--white);
  background-color: var(--orange);
  transition: all 0.2s;
}

.button:hover,
.button:focus-visible {
  opacity: 0.7;
}

.nav-close-btn {
  margin-left: 1.5rem;
  margin-bottom: 2rem;
  font-size: 1.75rem;
  color: var(--dark-grayish-blue);
}

.cart-btn {
  position: relative;
}

.cart-btn:hover path {
  fill: var(--black);
}

.checkout-btn {
  width: calc(100% - 3rem);
  margin-left: 1.5rem;
  margin-bottom: 1.5rem;
  border-radius: 8px;
  display: none;
}

.add-to-cart-btn {
  font-size: 1rem;
  font-weight: 700;
  border-radius: 12px;
  box-shadow: 0px 5px 30px -10px rgba(255, 125, 26, 0.84);
}

.lightbox-close-btn {
  position: absolute;
  top: -3rem;
  right: 0;
  font-size: 2rem;
  color: #FFF;
}

.cart {
  display: flex;
  flex-direction: column;
  background-color: var(--white);
  position: absolute;
  top: 58px;
  right: -16px;
  width: min(360px, 96vw);
  z-index: 1;
  border-radius: 8px;
  box-shadow: 0 15px 30px 5px rgba(144, 144, 144, 0.379);
  display: none;
}

.cart-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
}

.cart-content > img {
  max-width: 52px;
  border-radius: 4px;
}

.cart-article-title {
  display: block;
  margin-bottom: 0.5rem;
  color: var(--dark-grayish-blue);
}

.cart-article-title + div > * {
  font-size: 1.125rem;
  margin-right: 0.35rem;
  color: var(--dark-grayish-blue);
}

header {
  max-width: calc(1110px + 2rem);
  margin: auto;
}
header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid var(--dark-grayish-blue);
}

.header-left,
.header-right {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  gap: 1rem;
}

.header-right {
  gap: 1.5rem;
  position: relative;
}

.logo {
  display: block;
  position: relative;
  bottom: 1px;
}

.desktop-nav ul {
  margin: 0;
  padding: 0;
  padding-left: 2.5rem;
  column-gap: 2rem;
  display: none;
}
.desktop-nav a {
  display: block;
  padding: 3rem 0;
  max-height: 114px;
  transition: all 0.1s;
  color: var(--dark-grayish-blue);
}
.desktop-nav a:hover {
  border-bottom: 3px solid var(--orange);
}

.cart-quantity-badge {
  position: absolute;
  top: -7px;
  right: -10px;
  display: block;
  padding: 0.125rem 0.5rem;
  font-size: 0.625rem;
  line-height: 1;
  font-weight: 700;
  color: var(--white);
  background-color: var(--orange);
  border-radius: 8px;
  display: none;
}

.profile-img {
  height: 1.5rem;
  width: 1.5rem;
  border: 2px solid var(--white);
  border-radius: 100%;
  transition: all 0.2s;
}

.profile-img:hover {
  border: 2px solid var(--orange);
}

.lightbox {
  max-width: 40em;
  margin: auto;
  position: relative;
}

.lightbox-main {
  height: 80vw;
  max-height: 640px;
  position: relative;
}

.product-image-main {
  height: 80vw;
  max-height: 640px;
  width: 100%;
  object-fit: cover;
}

.arrow-left img,
.arrow-right img,
.arrow-left-popup img,
.arrow-right-popup img {
  padding-inline: 3px;
}

.arrow-left,
.arrow-left-popup,
.arrow-right,
.arrow-right-popup {
  background-color: var(--white);
  padding: 0.7rem;
  border-radius: 100%;
  position: absolute;
  top: calc(50% - 20px);
}

.arrow-left {
  left: 1rem;
}

.arrow-right {
  right: 1rem;
}

.lightbox-thumbnails {
  display: none;
  justify-content: space-between;
  gap: 2rem;
  margin-top: 2rem;
}
.lightbox-thumbnails img {
  width: 90px;
  border-radius: 12px;
  transition: all 0.2s;
}
.lightbox-thumbnails a {
  background-color: var(--white);
  border-radius: 12px;
}
.lightbox-thumbnails .active {
  border: 2px solid var(--orange);
  opacity: 0.5;
}

.lightbox-thumbnails a:hover img {
  opacity: 0.4;
}

.price-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 2rem 0;
}

.price {
  display: flex;
  align-items: center;
  gap: 1rem;
  font-size: 2rem;
  font-weight: 700;
  line-height: 1;
}

.discount {
  display: block;
  font-size: 1rem;
  line-height: 1;
  padding: 0.25rem 0.5rem;
  color: var(--orange);
  background-color: var(--orange);
  border-radius: 4px;
}

.old-price {
  font-weight: 700;
  text-decoration: line-through;
  color: var(--clr-gray-blue);
}

.add-to-cart-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
  margin-bottom: 4rem;
}

.quantity-selector {
  display: flex;
  justify-content: space-between;
  padding: 1.25rem;
  border-radius: 12px;
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 1;
  background-color: var(--dark-grayish-blue);
}

.overlay {
  position: absolute;
  inset: 0;
  z-index: 2;
  min-height: 110vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--clr-black-transparent);
  display: none;
}

.arrow-left-popup {
  left: -20px;
  top: calc(50% - 80px);
}

.arrow-right-popup {
  right: -20px;
  top: calc(50% - 80px);
}

.mobile-nav ul {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  z-index: 99;
  padding: 0;
  padding-top: 1.5rem;
  padding-right: 8rem;
  background-color: #FFF;
  box-shadow: 0 10px 500px 500px rgba(0, 0, 0, 0.75);
}
.mobile-nav a {
  padding: 1rem 1.5rem;
  font-weight: 700;
  color: #000;
}

.mobile-nav a:hover,
.mobile-nav a:active,
.mobile-nav a:focus-visible {
  border: none;
  color: var(--dark-grayish-blue);
}